export default async function SeriesPage({
  params,
}: {
  params: { id: number };
}) {
  const { id } = await params;
  const { error, data } = await getSeriesById(id);
  if (error) throw error;
  if (!data) notFound();

  const { dataPoints, metadata, measurement } = data;
  return (
    <div className="grid grid-cols-12 gap-4">
      <div className="bg-muted col-span-3 rounded border">
        <h2 className="text-4xl">MetaData</h2>
        <MetaDataTable metadata={metadata} />
      </div>
      <div className="bg-muted col-span-6 rounded border">
        {" "}
        <h2 className="text-4xl">Data Points</h2>
        <div>
          {dataPoints.map((dp) =>
            Object.entries(dp).map(([key, val], i) => (
              <p key={i}>
                <span>{key}:</span>
                <span>{JSON.stringify(val)}</span>
              </p>
            ))
          )}
        </div>
      </div>
      <div className="bg-muted col-span-3 rounded border"></div>
    </div>
  );
}

